/* Image */

.image {
  border-radius: _size(border-radius);
  border: 0;
  display: inline-block;
  position: relative;

  img {
    border-radius: _size(border-radius);
    display: block;
  }

  &.left,
  &.right {
    max-width: 40%;

    img {
      width: 100%;
    }
  }

  &.left {
    float: left;
    padding: 0 1.5em 1em 0;
    top: 0.25em;
  }

  &.right {
    float: right;
    padding: 0 0 1em 1.5em;
    top: 0.25em;
  }

  &.fit {
    display: block;
    margin: 0 0 _size(element-margin) 0;
    width: 100%;

    img {
      width: 100%;
    }
  }

  &.main {
    display: block;
    margin: 0 0 (_size(element-margin) * 1.5) 0;
    width: 100%;

    img {
      width: 100%;
    }
  }
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 767px) {
  .image {
    border-radius: _size(border-radius);
    border: 0;
    display: inline-block;
    position: relative;

    img {
      border-radius: _size(border-radius);
      display: block;
    }

    &.left,
    &.right,
    &.mobile-full {
      max-width: 100%;

      img {
        width: 100%;
      }
    }

    &.left,
    &.mobile-full {
      float: none;
      padding: 0 1.5em 1em 0;
      top: 0.25em;
    }

    &.right,
    &.mobile-full {
      float: none;
      padding: 0 0 1em 1.5em;
      top: 0.25em;
    }
  }
}
