// Misc.
$misc: (z-index-base: 10000, max-wrapper-styles: 6);

// Duration.
$duration: (menu: 0.35s, transition: 0.2s);
// Size.
$size: (
  border-radius: 5px,
  element-height: 2.75em,
  element-margin: 2em,
  inner: 55em,
  section-spacing: (large: 3em, medium: 2em, small: 1.75em),
  wrapper-edges: (large: 6.5em, medium: 4.75em, small: 2.5em)
);
// Font.
$font: (
  family: ('Source Sans Pro', 'Helvetica Neue', Arial, sans-serif),
  family-fixed:
    (
      'IBM Plex Mono',
      'SFMono-Regular',
      'Roboto Mono',
      Menlo,
      'Fira Mono',
      Courier,
      monospace
    ),
  family-heading:
    (
      'Nunito Sans',
      'Roboto Condensed',
      'Helvetica Neue',
      Roboto,
      Arial,
      sans-serif
    ),
  weight: 400,
  weight-bold: 700,
  weight-heading: 600,
  weight-heading-bold: 700,
  kern-heading: 0.1em,
  header-spacing: 0.06em
);
// Palette.
$palette: (
  bg: #2e3141,
  fg: #afb7c9,
  fg-bold: #afb7c9,
  fg-light: rgba(255, 255, 255, 0.35),
  border: rgba(255, 255, 255, 0.125),
  border-bg: rgba(255, 255, 255, 0.025),
  border2: rgba(255, 255, 255, 0.25),
  border2-bg: rgba(255, 255, 255, 0.075),
  accent: #4c5c96
);
